.navbar {
    background-color: #7393B3;
    width: 100%;
    height: 50px;
    display: flex;
    border-bottom: 1px solid lightgrey;
}

.navbar__logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 10px;
}

.navbar__logo > img {
    height: 50px;
    width: 50px;
}

.navbar__links {
    flex: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar__links #hidden {
    display: flex;
}

.navbar__links > button {
    display: none;
    margin-left: 20px;
}

.navbar__links {
    max-height: 80px;
}

.navbar__links > a {
    text-decoration: none;
    color: white;
    font-size: 20px;
    margin-left: 15px;
}

.navbar__links > a:hover {
    color: blanchedalmond;
}


.navbar__social {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 10px;
}

.navbar__social > button {
}


@media only screen and (max-width: 850px) {
    .navbar__links a {
        font-size: 18px;
    }
}

@media only screen and (max-width: 750px) {
    .navbar__links a {
        font-size: 15px;
    }
}

